.banner {
    border-radius: 25px;
    &__homeimg {
        background: url("../../assets/backheader.png") center/cover;
        border-radius: 25px;
        height: 111px;
        width: 100%;
        @include desktop-only {
            height: 223px;
            justify-content: center;
        }

        @include tablette-only {
            height: 200px;
        }
    }
    &__title {
        color: $color-white;
        background-color: rgb(000 000 000 / 0.3);
        height: 111px;
        border-radius: 25px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding: 20px;
        @include desktop-only {
            height: 223px;
            align-items: center;
            justify-content: center;
        }
        @include tablette-only {
            height: 200px;
            justify-content: center;
        }
    }
    &__aboutimg {
        background: url("../../assets/aboutbanner.png") center/cover;
        border-radius: 25px;
        height: 223px;
        width: 100%;
        opacity: 80%;
        @include desktop-only {
            height: 223px;
            justify-content: center;
        }
    }
}
